.switch {
  display: inline-block;
  position: relative;
}

.switchinput {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
}

.switchlabel {
  margin-left: -14px;
  position: relative;
  display: inline-block;
  width: 80px;
  height: 42px;
  background-color: #2b2b2b;
  border: 5px solid #5b5b5b;
  border-radius: 9999px;
  cursor: pointer;
  -webkit-transition: all 0.4s cubic-bezier(0.46, 0.03, 0.52, 0.96);
  transition: all 0.4s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}

.switchindicator {
  position: absolute;
  top: 50%;
  left: 51%;
  -webkit-transform: translate(-50%, -50%) translateX(-72%);
  -ms-transform: translate(-50%, -50%) translateX(-72%);
  -webkit-transform: translate(-50%, -50%) translateX(-72%);
  -ms-transform: translate(-50%, -50%) translateX(-72%);
  transform: translate(-50%, -50%) translateX(-72%);
  display: block;
  width: 26px;
  height: 26px;
  background-color: #7b7b7b;
  border-radius: 9999px;
  box-shadow: 10px 0px 0 0 rgba(#000000, 0.2) inset;
}
.switchindicator::before,
.switchindicator::after {
  position: absolute;
  content: '';
  display: block;
  background-color: #ffffff;
  border-radius: 9999px;
}

.switchindicator::before {
  top: 7px;
  left: 7px;
  width: 9px;
  height: 9px;
  background-color: #ffffff;
  opacity: 0.6;
}

.switchindicator::after {
  bottom: 8px;
  right: 6px;
  width: 6px;
  height: 6px;
  background-color: #ffffff;
  opacity: 0.8;
}

.switchdecoration {
  position: absolute;
  top: 65%;
  left: 50%;
  display: block;
  width: 5px;
  height: 5px;
  background-color: #ffffff;
  border-radius: 9999px;
  animation: twinkle 0.8s infinite -0.6s;
}

.switchdecoration::after,
.switchdecoration::before {
  position: absolute;
  display: block;
  content: '';
  width: 5px;
  height: 5px;
  background-color: #ffffff;
  border-radius: 9999px;
}
.switchdecoration::before {
  top: -20px;
  left: 10px;
  opacity: 1;
  animation: twinkle 0.6s infinite;
}

.switchdecoration::after {
  top: -7px;
  left: 30px;
  animation: twinkle 0.6s infinite -0.2s;
}

@keyframes twinkle {
  50% {
    opacity: 0.2;
  }
}

.switchindicator,
.switchindicator::after,
.switchindicator::before {
  transition: all 0.4s cubic-bezier(0.46, 0.03, 0.52, 0.96);
}

.switchinput:checked + .switchlabel {
  background-color: #8fb5f5;
  border-color: #347cf8;

  .switchindicator {
    background-color: #ecd21f;
    box-shadow: none;
    transform: translate(-50%, -50%) translateX(72%);

    &::before,
    &::after {
      display: none;
    }
  }

  .switchdecoration {
    top: 50%;
    transform: translate(0%, -50%);
    animation: cloud 8s linear infinite;

    width: 20px;
    height: 20px;

    &::before {
      width: 10px;
      height: 10px;
      top: auto;
      bottom: 0;
      left: -8px;
      animation: none;
    }

    &::after {
      width: 15px;
      height: 15px;
      top: auto;
      bottom: 0;
      left: 16px;
      animation: none;
    }

    &,
    &::before,
    &::after {
      border-radius: 9999px 9999px 0 0;
    }

    &::after {
      border-bottom-right-radius: 9999px;
    }
  }
}
